



















































































































































































    export default {
        components: {
        },
        data() {
            return {
                isClickChange: false,
                tabIndex: 0,
                newsitems: [123,456],
                tabBars: [{
                    name: '短片',
                    id: 'duanpian'
                }, {
                    name: '评论(1210)',
                    id: 'pinglun'
                }],
				showPianduan: true,
				videoList: [1,2,3,4,5,6]
            }
        },
        onLoad: function() {
            // this.newsitems = this.randomfn()
        },
        methods: {
			goBack() {
				uni.navigateBack({
					delta: 1
				});
			},
            changeTab(e) {
                let index = e.target.current;
                if (this.isClickChange) {
                    this.tabIndex = index;
                    this.isClickChange = false;
                    return;
                }
                this.isClickChange = false;
                this.tabIndex = index; //一旦访问data就会出问题
            },
            tapTab(e) { //点击tab-bar
                if (this.tabIndex === e.target.dataset.current) {
                    return false;
                } else {
                    this.isClickChange = true;
                    this.tabIndex = e.target.dataset.current
                }
            },
			goPianduan() {
				if(!this.showPianduan) {
					this.showPianduan = !this.showPianduan
				}
			},
			goFenji() {
				if(this.showPianduan) {
					this.showPianduan = !this.showPianduan
				}
			},
			dianzan(e) {
				console.log(e)
			},
			goYingjuzhuye() {
				uni.navigateTo({
					url: '../yingjuzhuye/yingjuzhuye'
				});
			}
        }
    }



